import { Link } from 'react-router-dom';
import { useState, useEffect, useRef } from 'react';
import './buildpage.css'
import RandomDecor from './component/randomdector';

function BuildPage() {
    const time = 6;
    // 15
    const [seconds, setSeconds] = useState(time);

    // refDOMdocument.getElementById
    const progressBarRef = useRef(null);    // 
    const countdownElementRef = useRef(null); // 
    const timerIdRef = useRef(null);       // IDref
    
    useEffect(() => {
        // 
        const updateProgress = () => {
            // 0-100
            const progress = ((time - seconds) / time) * 100;
            
            // DOM
            if (progressBarRef.current) {
                progressBarRef.current.style.width = `${progress}%`;
            }
            
            // 
            if (countdownElementRef.current) {
                countdownElementRef.current.textContent = seconds;
            }

            // setState
            setSeconds(prev => prev - 1);

            // 
            if (seconds <= 0) {
                window.location.href = '/home'; // 
                // react-routernavigate
                // navigate('/home');
            } else {
                // 1
                timerIdRef.current = setTimeout(updateProgress, 1000);
            }
        };

        // 1
        timerIdRef.current = setTimeout(updateProgress, 1000);

        // 
        return () => {
            if (timerIdRef.current) {
                clearTimeout(timerIdRef.current);
            }
        };
    }, [seconds]);

    return (
        <section className="buildpage-container">
            <RandomDecor />
            {/*  */}
            <div className="construction-card">
                <h1 className="buildpage-title"></h1>
                <p className="buildpage-textual"> ...</p>
                
                {/* refDOM */}
                <div className="countdown" ref={countdownElementRef}>{ time}</div>
                
                {/*  */}
                <div className="progress-container">
                    <div className="progress-bar" ref={progressBarRef}></div>
                </div>
                
                <p className="buildpage-textual">
                    <br />
                    {/* react-routerLink */}
                    <Link to="/home" className="manual-jump"> </Link>
                </p>
            </div>
        </section>
    );

    
}

export default BuildPage;